import "./styles.css";

export default function App() {
  const maxQuestion = 100;
  const CreateComponent = () => {
    const tempArr = [];
    for (let i = 0; i < maxQuestion; i++) {
      tempArr.push(
        <div style={{ margin: "10px" }}>
          <span style={{ marginRight: "10px" }}> {i + 1 + ")"}</span>
          <span className="optionWrapper">
            A
            <input type="radio" id="male" name="option" value="a" />
          </span>
          <span className="optionWrapper">
            B
            <input type="radio" id="male" name="option" value="a" />
          </span>
          <span className="optionWrapper">
            C
            <input type="radio" id="male" name="option" value="a" />
          </span>
          <span className="optionWrapper">
            D
            <input type="radio" id="male" name="option" value="a" />
          </span>{" "}
        </div>
      );
    }
    return tempArr;
  };
  return (
    <div className="App">
      <h1>Answer sheet</h1>
      <CreateComponent />
    </div>
  );
}
